
.JourneyContainer{
  position: relative;
  width: 100%;
  height: 100vh; /* Ensure the container is at least 100vh */
  overflow: hidden;
}

.background-video {
  position: absolute; /* Ensures the video stays in place */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: fill;
}
